import React, { Component, createElement } from "react";
import './index.scss'

const BLACK_LIST_EL_FOR_CHILD = {
  "iframe": true,
  "video": true,
  "img": true ,
  "br": true,
  "input": true,
  "textarea": true,
  "source": true
}
const STYLE_TYPE_CHECK = ["backgroundImage"
// , "maskImage", "webkitMaskImage"
]

export class HtmlElement extends Component {
  constructor(props) {
    super(props);
  }
  
  getElementConfig = (props) => {
    const res = {};
    const media = {...props.media, ...props.config, "key-id":props.id};
    if(props.className){
      res["className"] = `${props.className}`
    }
    if(media["fox-onclick"]){
      res["onClick"] = this.onClick.bind(this)
    }
    for (const key in media) {
      if(media[key]){
        res[key] = media[key]
      }
    }
    res["style"] = this.getStyle(media)
    return res
  }

  onClick = () => {
    this.props.onClickTrigger?.(this.props)
  }

  getStyle = (attr) => {
    const style = {}
    for (let i = 0; i < STYLE_TYPE_CHECK.length; i++) {
      const key = STYLE_TYPE_CHECK[i]
      if(attr[key]){
        style[key] = `url(${attr[key]})`;
      }
    }
    return style
  }
  componentDidMount = () => {
    if(this.props.className?.match(/triggerdone/)){
      this.props.onTriggerDone?.(this.props)
    }
  }

  render() {
    const { props, state } = this;
    if(BLACK_LIST_EL_FOR_CHILD[props.type]){
      return createElement(
        props.type,
        { ...this.getElementConfig(props) },
        props.children,
      );
    }
    return createElement(
      props.type,
      { ...this.getElementConfig(props) },
      props.media.text,
      props.children,
    );
  }
}
